// ==========================================================================
// Base Styles
// ==========================================================================

// Layout

* {
	box-sizing: border-box;
}


.container {
	width: 1200px;
	margin-left: auto;
	margin-right: auto;
	overflow: hidden;
}

// Body

body {
	font-family: Poppins, sans-serif;
	font-size: 1rem;
	letter-spacing: 0.4px;
	color: #666;
	margin: 0;
}

/* Links */

a:link {
	color: $white;
	text-decoration: none;
}

a:hover {
	text-decoration: underline;
}

a:visited {
	color: $white;
}

a:active {
	color: $black;
}

// Header

.header-support {
	background-color: #4183d7;
}
.header-contact {
	background-color: #999aa2;
}
.navbar {
	display: flex;
	background-color: $white#333;

	a {
		color: $white;
		padding: 14px 20px;
		text-decoration: none;
		text-align: center;
	}
}

@media screen and (max-width: 700px) {
	.row,
	.navbar {
		flex-direction: column;
	}
}
.topnav .search-container {
	float: right;
}

.topnav input[type="text"] {
	padding: 6px;
	margin-top: 8px;
	font-size: 17px;
	border: none;
}

.topnav .search-container button {
	float: right;
	padding: 6px 10px;
	margin-top: 8px;
	margin-right: 16px;
	background: #ddd;
	font-size: 17px;
	border: none;
	cursor: pointer;
}

.topnav .search-container button:hover {
	background: #ccc;
}

@media screen and (max-width: 700px) {
	.topnav .search-container {
		float: none;
	}
	.topnav a,
	.topnav input[type="text"],
	.topnav .search-container button {
		float: none;
		display: block;
		text-align: left;
		width: 100%;
		margin: 0;
		padding: 14px;
	}
	.topnav input[type="text"] {
		border: 1px solid #ccc;
	}
}
// Footer

footer {
	font-size: 16px;
	font-family: Poppins, sans-serif;
	background-color: $bunting;
	display: block;
	flex-direction: column;
	color: $white;
	flex: 1 0 20%;

	ul {
		list-style: none;
		padding: 0;
		margin: 0;
	}

	a:link {
		color: $white;
		text-decoration: none;
	}

	a:hover {
		text-decoration: underline;
	}

	a:visited {
		color: $white;
	}

	a:active {
		color: $black;
	}

	.footer-row {
		flex-wrap: wrap;
		display: flex;

		.services,
		.offices {
			display: flex;
			justify-content: space-between;
		}

		.service,
		.office {
			width: 225px;
		}
	}

	.bottom {
		display: flex;
		justify-content: space-between;
		border-top: 1px solid $white;
		padding: 1%;

		.license {
			color: $white;
			text-decoration: none;
			list-style: none;
			display: flex;

			span {
				margin: 0 5px 0 5px;
			}
		}

		.social-media {
			display: flex;
		}
	}
}

.visible-xs {
	display: none;
}
